<template>
  <div id="app" >
    <v-app id="inspire" :style="{'background-image': 'url(' + require('../assets/background-login.jpg') + ')'}" style="background-size: cover;">
      <v-content >
        <v-container fluid fill-height>
          <v-layout align-center justify-center>
            <v-flex xs10 sm8 md3>
              <v-card class="elevation-12">
                <v-app-bar flat class="toolbar green" height="50">
                  <img
                    @click="openWebsite()"
                    class="logo mr-2"
                    :src="logo"
                    width="95px"
                  />
                  <span class="white--text title"
                    >Street View Client</span
                  >
                  <v-spacer></v-spacer>
                </v-app-bar>
                <v-progress-linear
                  :active="isLoading"
                  indeterminate
                  color="green"
                ></v-progress-linear>
                <v-alert class="ma-2" outlined v-if="error" type="error">
                  {{ error }}
                </v-alert>
                <v-card-text>
                  <v-form
                    @keyup.native.enter="pressed"
                    @submit.prevent="pressed"
                    v-model="valid"
                    lazy-validation
                  >
                    <v-text-field
                      label="Email"
                      name="login"
                      v-model="email"
                      :rules="emailRules"
                      prepend-icon="person"
                      type="text"
                      required
                      :disabled="isLoading"
                    ></v-text-field>

                    <v-text-field
                      id="password"
                      label="Password"
                      name="password"
                      v-model="password"
                      prepend-icon="lock"
                      type="password"
                      required
                      :disabled="isLoading"
                    ></v-text-field>
                  </v-form>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn
                    :disabled="isLoading"
                    @click="pressed"
                    dark
                    color="green"
                    >Login</v-btn
                  >
                </v-card-actions>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
      </v-content>
    </v-app>
  </div>
</template>

<script>
import * as firebase from "firebase/app";
import "firebase/auth";
export default {
  data() {
    return {
      logo: "img/yec_logo.png",
      background: "img/background-login.jpg",
      valid: true,
      email: "",
      emailRules: [
        v => !!v || "E-mail is required",
        v => /.+@.+\..+/.test(v) || "E-mail must be valid"
      ],
      password: "",
      error: "",
      isLoading: false
    };
  },
  methods: {
    pressed() {
      if (!this.valid) {
        return;
      }
      this.error = "";
      this.isLoading = true;

      firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.password)
        .then(() => {
          this.isLoading = false;
          this.$router.replace({ name: "home" });
        })
        .catch(error => {
          this.error = error;
          this.isLoading = false;
        });
    },
    openWebsite() {
      window.open("https://yukonenergy.ca/", "_blank");
    }
  }
};
</script>

<style lang="scss" scoped></style>
