<template>
  <v-expansion-panels
    class="elevation-3"
    dark
    style="position:absolute;bottom:35px;right:10px;maxWidth: 200px;"
  >
    <v-expansion-panel style="background-color: white;">
      <v-expansion-panel-header>
        Legend
        <template v-slot:actions>
          <v-icon small>$vuetify.icons.expand</v-icon>
        </template>
      </v-expansion-panel-header>
      <v-expansion-panel-content eager style="max-height:400px;">
        <template v-for="(item, index) in layers">
          <div
            :key="index"
            v-show="item.getVisible() === true"
            style="padding-right:10px;"
          >
            <template v-if="getLayerType(item) === 'WMS'">
              <p class="grey--text text--darken-2 pb-0 mb-1 mt-2 subtitle-2">
                {{ item.get("name") }}
              </p>
              <v-divider></v-divider>
              <!-- Parent layer can have multiple child layers, so we need to loop through -->
              <template
                v-for="(layerName, index2) in item
                  .getSource()
                  .getParams()
                  .LAYERS.split(',')"
              >
                <div :key="index2">
                  <img
                    style="max-width: 100%;"
                    :src="getWMSLegendImageUrl(item, layerName)"
                    class="white--text mt-0 pt-0"
                  />
                  <br />
                </div>
              </template>
            </template>

            <template v-show="getLayerType(item) === 'WFS'">
              <v-container>
                <v-row>
                  <img
                    style="max-width: 100%;"
                    :src="getVectorLayerImageUrl(item)"
                    class="white--text ml-2 mt-0 pt-0 mr-5"
                  />
                  <span
                    class="grey--text text--darken-2 pb-0 mb-1 mt-2 subtitle-2"
                    >{{ item.get("name") }}
                  </span>
                </v-row>
              </v-container>
            </template>
          </div>
        </template>
        <!-- FOR MAPILLARY LAYERS -->
        <template v-for="(item, index) in mapillaryIcons">
          <div
            :key="index + 100"
            style="padding-right:10px;"
            v-if="
              !item.removeWithLayer ||
                (mapillaryLayer &&
                  mapillaryLayer.getVisible() === true &&
                  item.removeWithLayer)
            "
          >
            <v-container class="py-1">
              <v-row>
                <img
                  :class="{ 'mr-3': item.icon === 'mapillary-location' }"
                  style="max-width:30%;"
                  :src="getVectorLayerImageUrl(item.icon)"
                  class="white--text
                  mt-0 pt-0 mr-2"
                />
                <span
                  class="grey--text text--darken-2 pb-0 pt-1 mb-1 mt-2 subtitle-2"
                  >{{ item.title }}
                </span>
              </v-row>
            </v-container>
          </div></template
        >
      </v-expansion-panel-content>
    </v-expansion-panel>
  </v-expansion-panels>
</template>
<script>
import { Mapable } from "../../../../mixins/Mapable";
import { getAllChildLayers, getWMSLegendURL } from "../../../../utils/Layer";
// import { getStyleImage } from "../../../../utils/Legend";
import { getLayerType, getStyleImage } from "../../../../utils/Layer";
import { EventBus } from "../../../../EventBus";

export default {
  mixins: [Mapable],
  name: "map-legend",
  data: () => ({
    layers: [],
    mapillaryIcons: [
      { icon: "mapillary-location", title: "Current Location" },
      {
        icon: "mapillary-active",
        title: "Selected Sequence",
        removeWithLayer: true
      },
      {
        icon: "mapillary-public",
        title: "Other Sequences",
        removeWithLayer: true
      }
    ],
    mapillaryLayer: null
  }),
  methods: {
    getLayerType,
    /**
     * This function is executed, after the map is bound (see mixins/Mapable)
     */
    onMapBound() {
      const me = this;
      const allLayers = getAllChildLayers(me.map);
      me.layers = allLayers.filter(
        layer =>
          (layer.getSource().serverType_ === "geoserver" ||
          ["VECTOR", "ESRI"].includes(layer.get("type"))) &&
          layer.get("displayInLegend") !== false
      );
    },
    getWMSLegendImageUrl(item, layerName) {
      let layerUrl = item.getSource().getUrl();
      if (layerUrl.startsWith("/")) {
        layerUrl = window.location.origin + layerUrl;
      }
      const legedUrl = getWMSLegendURL(
        layerUrl,
        layerName,
        undefined,
        undefined,
        undefined,
        undefined,
        item.getSource().serverType_,
        120,
        undefined,
        undefined,
        undefined,
        "en"
      );
      return legedUrl;
    },
    getVectorLayerImageUrl(item) {
      let name;
      if (typeof item === "object") {
        name = item.get("name");
      } else {
        name = item;
      }
      const url = `img/vector-legend-icon/${name}.PNG`;
      return url;
    },
    renderVectorLegendImage(item, ref) {
      setTimeout(() => {
        getStyleImage(
          {
            typeGeom: item
              .getSource()
              .getFeatures()[0]
              .getGeometry()
              .getType(),
            style: item.getStyle(),
            feature: item.getSource().getFeatures()[0]
          },
          this.$refs[ref][0]
        );
      }, 3000);
    }
  },
  mounted() {
    EventBus.$on("mapillaryLayerAdded", layer => {
      this.mapillaryLayer = layer;
    });
  }
};
</script>
<style lang="css" scoped>
.v-expansion-panel-header {
  min-height: 30px;
  padding: 5px;
  background-color: #4caf50;
}

.v-expansion-panel--active>.v-expansion-panel-header {
    min-height: 30px;
}

.v-expansion-panel-content >>> .v-expansion-panel-content__wrap {
  padding: 2px 0px 0px 5px;
}
</style>
