import Vue from "vue";
import vuetify from "@/plugins/vuetify";
import "./plugins/vuescroll";
import App from "./App.vue";
import router from "./router";
import firebase from "firebase/app";
import 'firebase/storage';  // <----

require("../node_modules/ol/ol.css");
require("./assets/scss/app.scss");

// Firebase authentication
  var firebaseConfig = {
    apiKey: "AIzaSyAOqcRbxxsB5gSou_t0yfMaBL4qhrsTqR8",
    authDomain: "underhill-1973e.firebaseapp.com",
    databaseURL: "https://underhill-1973e.firebaseio.com",
    projectId: "underhill-1973e",
    storageBucket: "underhill-1973e.appspot.com",
    messagingSenderId: "792391738711",
    appId: "1:792391738711:web:cb513c38d4e365022bca12",
    measurementId: "G-8RGFXDGQC9"
  };
// Initialize Firebase
firebase.initializeApp(firebaseConfig);

// Detect isEmbedded state by attribute embedded and
// make accessible for all components
// recommended by https://vuejs.org/v2/cookbook/adding-instance-properties.html
Vue.config.productionTip = false;
const appEl = document.querySelector("#app");
Vue.prototype.$isEmbedded = appEl.hasAttribute("embedded");


var storage = firebase.storage();

firebase.auth().onAuthStateChanged(() => {
  // Get config file from storage
  storage
    .ref("config/app-conf.json")
    .getDownloadURL()
    .then(url => {
      fetch(url)
        .then(function(response) {
          return response.json();
        })
        .then(function(appConfig) {
          // make app config accessible for all components
          Vue.prototype.$appConfig = appConfig;
          new Vue({
            vuetify,
            router,
            render: h => h(App)
          }).$mount("#app");
        });
    });
});
