<template>
  <div>
    <v-tooltip left>
      <template v-slot:activator="{ on }">
        <v-btn
          class="mx-2 signout-button"
          style="top:10px;"
          v-on="on"
          fab
          dark
          small
          color="green"
          @click="signOut"
        >
          <v-icon medium>fas fa-sign-out-alt</v-icon>
        </v-btn>
      </template>
      <span>Sign Out</span>
    </v-tooltip>
  </div>
</template>
<script>
import * as firebase from "firebase/app";
import "firebase/auth";

export default {
  name: "sign-out",
  methods: {
    signOut() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.replace({ name: "login" });
        });
    }
  }
};
</script>
<style lang="css" scoped>
.signout-button {
  position: absolute;
  right: 8px;
  z-index: 1;
}
</style>
