<template>
  <div id="mapillary-container">
    <mapillary-image-preview
      ref="imagePreview"
      :imageUrl="previewImageUrl"
      v-show="previewImageUrl.length > 1"
    />
  </div>
</template>
<script>
import { Viewer } from "mapillary-js";

import { MarkerComponent, PopupComponent } from "mapillary-js";

//Ol imports
import OlVectorLayer from "ol/layer/Vector";
import OlVectorSource from "ol/source/Vector";
import OlPoint from "ol/geom/Point";
import OlVectorTileLayer from "ol/layer/VectorTile";
import OlVectorTileSource from "ol/source/VectorTile";
import OlOverlay from "ol/Overlay";
import { createXYZ } from "ol/tilegrid";
import OlFeature from "ol/Feature";
import MVT from "ol/format/MVT";
import { toLonLat, fromLonLat } from "ol/proj";
import { unByKey } from "ol/Observable";
import { getFlatLayers } from "../../../utils/Layer";
import { debounce } from "../../../utils/Helpers";
import {
  geojsonToFeature,
  findClosestFeatureToCoordinate,
  calculateBearing
} from "../../../utils/MapUtils";

//Style and map object import
import { mapillaryStyleDefs } from "../../../style/OlStyleDefs";
import { EventBus } from "../../../EventBus";

// Image preview component import (enabled on hover)
import MapillaryImagePreview from "./controls/MapillaryImagePreview";

//Others
import http from "../../../services/http";

import "mapillary-js/dist/mapillary.min.css";

export default {
  name: "app-mapillary",
  components: {
    "mapillary-image-preview": MapillaryImagePreview
  },
  data() {
    return {
      // Keys
      cliendId: "VDhYc2NwU3NfM19XNjczaTdXRlF1ZzpmN2RkOGU1ZGY2NTE2ODI4",
      key: "vA_4gGZewNZKfzlcWppQ7Q", // Start image key
      organizationKey: "GWPwbGxhu82M5HiAeeuduH",
      baseOverlayUrl:
        "https://d25uarhxywzl1j.cloudfront.net/v0.1/{z}/{x}/{y}.mvt",
      startSequenceKey: "gcsujv9t4moejxt6t6h37g",
      // Mapillary API
      mapillaryImgAPI: "https://a.mapillary.com/v3/images",
      // Preview Image url
      previewImageUrl: "",
      // Mapillary viewer
      mapillary: null,
      // Features
      highlightFeature: null,
      pointFeature: null,
      // Layers
      movePointLayer: null,
      baseOverlayerLayer: null,
      hoverHighlightLayer: null,
      // Listener keys
      mapClickListenerKey: null,
      mapHoverListenerKey: null,
      // Overlays
      imagePreviewOverlay: null
      //
    };
  },
  mounted() {
    if (!this.mapillary && this.map) {
      this.init();
    }
  },
  created() {
    this.map = null;
    EventBus.$on("ol-map-mounted", olMap => {
      // make the OL map accesible in this component
      this.map = olMap;
      if (this.onMapBound) {
        this.onMapBound();
      }
    });
  },
  methods: {
    onMapBound() {
      if (!this.mapillary) {
        this.init();
      }
    },
    /**
     * Init Mapillary Component
     */
    init() {
      this.mapillary = new Viewer(
        "mapillary-container",
        this.cliendId,
        this.key,
        {
          component: {
            cover: false,
            marker: {
              visibleBBoxSize: 1000
            },
            popup: true
          }
        }
      );
      window.addEventListener("resize", this.resize);
      this.createFeatureOverlay();
      this.createImagePreviewOverlay();
      this.createMovePointLayer();
      this.createBaseOverlayLayer();
      this.createHoverHighlightLayer();
      this.addInteractions();
      this.mapClickListenerKey = this.map.on("click", this.onClick);
      this.mapillary.on(Viewer.nodechanged, this.mapillaryChanged);
      this.mapillary.on(Viewer.moveend, this.mapillaryMoveend);
      mapillaryStyleDefs.activeSequence = this.startSequenceKey;
      EventBus.$on("featureClicked", this.goToOlFeature);
      EventBus.$on("layerItemSelected", this.layerItemSelected);

      // Add layers as overlays (only points can be added)
      let localLayers = [];
      this.map.getLayers().forEach(layer => {
        if (layer.get("name") === "Operational Layers") {
          localLayers = [...getFlatLayers(layer)];
        }
      });

      localLayers.forEach(layer => {
        if (layer.get("showInMapillary")) {
          this.pointLayerSource = layer.getSource();
          layer.getSource().on("change", () => {
            if (layer.getSource().getState() === "ready") {
              this.createMarkers(layer.getSource().getFeatures());
            }
          });
        }
      });

      // ADD HOVER EVENT FOR MARKERS
      this.markerComponent = this.mapillary.getComponent("marker");
      this.popupComponent = this.mapillary.getComponent("popup");
      this.canvasContainerEl = this.mapillary.getCanvasContainer();
      this.popup = new PopupComponent.Popup({
        clean: true,
        offset: 14
      });

      this.popupComponent.add([this.popup]);
      // Create popup content
      this.popupDiv = document.createElement("div");
      this.popupDiv.style.backgroundColor = "#fff";
      this.popupDiv.style.border = "2px solid";
      this.popupDiv.style.borderRadius = "5px";
      this.popupDiv.style.maxWidth = "240px";
      this.popupDiv.style.color = "#FF0000";
      this.popupDiv.style.padding = "5px 10px";
      this.popup.setDOMContent(this.popupDiv);
      // Add events
      this.mapillary.on("mousemove", this.showPopup);
      this.mapillary.on("mouseover", this.showPopup);
      this.mapillary.on("mouseup", this.showPopup);
      // Reset hover state on mouse out
      this.mapillary.on("mouseout", () => {
        this.removePopups();
      });

      // Reset dragging position if users moves the marker reset it.  (workaround)
      this.markerComponent.on("dragend", e => {
        const markerId = e.marker.id;
        const feature = this.pointLayerSource.getFeatureById(markerId);
        if (feature) {
          const lonLat = toLonLat(feature.getGeometry().getCoordinates());
          const marker = new MarkerComponent.SimpleMarker(
            markerId,
            {
              lat: lonLat[1],
              lon: lonLat[0]
            },
            { interactive: true }
          );
          this.markerComponent.add([marker]);
        }
      });
    },

    /**
     * Display popup in marker
     */
    showPopup(e) {
      this.markerComponent.getMarkerIdAt(e.pixelPoint).then(markerId => {
        if (markerId) {
          this.canvasContainerEl.style.cursor = "pointer";
        } else {
          this.canvasContainerEl.style.cursor = "";
        }
        this._renderPopup(markerId, e.basicPoint);
      });
    },

    _renderPopup: debounce(function(markerId, basicCoord) {
      if (markerId === this.currentActiveMarkerId) {
        return;
      }
      if (markerId === null) {
        this.removePopups();
        return;
      }
      this.currentActiveMarkerId = markerId;
      const marker = this.markerComponent.get(markerId);
      if (!marker) return;
      if (!basicCoord) return;
      if (this.pointLayerSource) {
        const feature = this.pointLayerSource.getFeatureById(markerId);
        if (feature) {
          this.popupDiv.innerHTML = feature.get("PopupInfo");
          this.popup.setBasicPoint(basicCoord);
          this.popupComponent.add([this.popup]);
        }
      }
    }, 50),

    removePopups() {
      this.popupComponent.removeAll();
      this.currentActiveMarkerId = null;
    },

    /** ----------------------- */

    createMarkers(features) {
      const markers = [];
      features.forEach(feature => {
        if (feature.getGeometry().getType() === "Point") {
       //   const popupInfo = feature.get("PopupInfo").replace(/<a\b[^>]*>(.*?)<\/a>/g, "")
          const coordinate = feature.getGeometry().getCoordinates();
          const lonLat = toLonLat(coordinate);
          // Create a non interactive simple marker with default options
          const marker = new MarkerComponent.SimpleMarker(
            feature.getId(),
            {
              lat: lonLat[1],
              lon: lonLat[0]
            },
            { interactive: true }
          );
          markers.push(marker);
        }
      });

      this.markerComponent.removeAll();
      this.markerComponent.add(markers);
    },
    /**
     * Overlay style
     */
    createFeatureOverlay() {
      this.featureOverlay = new OlVectorLayer({
        source: new OlVectorSource(),
        map: this.map,
        style: [
          mapillaryStyleDefs.wifiStyle,
          mapillaryStyleDefs.circleSolidStyle
        ]
      });
    },

    /**
     * Show popup for the get info module.
     */
    createImagePreviewOverlay() {
      this.imagePreviewOverlay = new OlOverlay({
        element: this.$refs.imagePreview.$el,
        autoPan: false,
        autoPanMargin: 40,
        autoPanAnimation: {
          duration: 250
        }
      });
      this.map.addOverlay(this.imagePreviewOverlay);
    },

    /**
     * Radar point layer
     */
    createMovePointLayer() {
      this.pointFeature = new OlFeature({
        geometry: null
      });
      this.pointFeature.setStyle([
        mapillaryStyleDefs.wifiStyle,
        mapillaryStyleDefs.circleSolidStyle
      ]);
      this.movePointLayer = new OlVectorLayer({
        zIndex: 10,
        source: new OlVectorSource({ features: [this.pointFeature] })
      });
      this.map.addLayer(this.movePointLayer);
    },

    /**
     * Overlay layer (sequences, images)
     */
    createBaseOverlayLayer() {
      const me = this;
      this.baseOverlayerLayer = new OlVectorTileLayer({
        name: "Mapillary Baselayer",
        displayInLayerList: true,
        group: "Operational Layers",
        source: new OlVectorTileSource({
          attributions: "© Mapillary",
          format: new MVT(),
          tileGrid: createXYZ({ maxZoom: 14 }),
          tilePixelRatio: 16,
          opacity: 0.7,
          url: this.baseOverlayUrl,
          tileLoadFunction: function(tile, url) {
            tile.setLoader(function(extent, resolution, projection) {
              fetch(url).then(function(response) {
                response.arrayBuffer().then(function(data) {
                  const format = tile.getFormat(); // ol/format/MVT configured as source format
                  let features = format.readFeatures(data, {
                    extent: extent,
                    featureProjection: projection
                  });
                  features = features.filter(f => {
                    return (
                      f.getProperties().organization_key === me.organizationKey
                    );
                  });
                  tile.setFeatures(features);
                });
              });
            });
          }
        }),
        style: mapillaryStyleDefs.baseOverlayStyle(this.map)
      });

      this.map.addLayer(this.baseOverlayerLayer);
      EventBus.$emit("mapillaryLayerAdded", this.baseOverlayerLayer);
    },

    /**
     * Create hover highlight layers
     */
    createHoverHighlightLayer() {
      this.hoverHighlightLayer = new OlVectorLayer({
        zIndex: 20,
        name: "mapillaryHighlightLayer",
        source: new OlVectorSource(),
        style: mapillaryStyleDefs.highlightStyle
      });
      this.map.addLayer(this.hoverHighlightLayer);
    },

    /**
     * Overlay layer interactions
     */
    addInteractions() {
      this.mapHoverListenerKey = this.map.on("pointermove", evt => {
        let features = this.map.getFeaturesAtPixel(evt.pixel, {
          layerFilter: candidate => {
            if (candidate.get("name") === "Mapillary Baselayer") {
              return true;
            }
            return false;
          }
        });
        this.hoverHighlightLayer.getSource().clear();
        if (
          features.length > 0 &&
          features[0].getProperties().layer === "mapillary-images"
        ) {
          this.previewImageUrl = `https://images.mapillary.com/${features[0].get(
            "key"
          )}/thumb-320.jpg`;
          this.imagePreviewOverlay.setPosition(
            features[0].getFlatCoordinates()
          );
          const feature = new OlFeature({
            geometry: new OlPoint(features[0].getFlatCoordinates())
          });
          feature.setProperties(features[0].getProperties());
          this.hoverHighlightLayer.getSource().addFeature(feature);
          this.map.getTarget().style.cursor = "pointer";
        } else {
          this.previewImageUrl = "";
          this.imagePreviewOverlay.setPosition(undefined);
          this.map.getTarget().style.cursor = "";
        }
      });
    },

    /**
     * Click event handler
     */
    onClick(evt) {
      this.hoverHighlightLayer.getSource().clear();
      const feature = this.map.forEachFeatureAtPixel(
        evt.pixel,
        function(feature) {
          return feature;
        },
        {
          layerFilter: layer => {
            return layer === this.baseOverlayerLayer;
          },
          hitTolerance: 5
        }
      );

      if (feature) {
        if (feature.get("layer") === "mapillary-sequences") {
          return;
        }
        var bearing = feature.get("ca");
        this.mapillary.moveToKey(feature.get("key"));
        this.featureOverlay.setStyle(
          mapillaryStyleDefs.updateBearingStyle(bearing)
        );
        mapillaryStyleDefs.activeSequence = feature.get("skey");
      } else {
        return;
      }
      //Update layer
      this.baseOverlayerLayer.changed();
    },

    /**
     * Mapillary changed event handler
     */
    mapillaryChanged(node) {
      if (this.featureOverlay.getVisible()) {
        this.featureOverlay.setVisible(false);
      }
      const lonLat = new fromLonLat([
        node.originalLatLon.lon,
        node.originalLatLon.lat
      ]);
      this.map.getView().animate({
        center: lonLat,
        duration: 400
      });
      if (this.pointFeature.getGeometry() === null) {
        this.pointFeature.setGeometry(new OlPoint(lonLat));
      } else {
        this.pointFeature.getGeometry().setCoordinates(lonLat);
      }
      if (mapillaryStyleDefs.activeSequence !== node.sequenceKey) {
        mapillaryStyleDefs.activeSequence = node.sequenceKey;
        this.baseOverlayerLayer.changed();
      }
    },

    /**
     * Mapillary movend event handler (Used for getting bearing angle)
     */
    mapillaryMoveend() {
      this.mapillary.getBearing().then(bearing => {
        if (this.pointFeature) {
          this.pointFeature.setStyle(
            mapillaryStyleDefs.updateBearingStyle(bearing)
          );
        }
      });
    },

    /**
     * Find closest image to Ol feature
     */

    goToOlFeature(feature) {
      const geom = feature.getGeometry();
      if (geom.getType() === "Point") {
        const coordinates = toLonLat(geom.getCoordinates());
        // https://a.mapillary.com/v3/images?client_id=VDhYc2NwU3NfM19XNjczaTdXRlF1ZzpmN2RkOGU1ZGY2NTE2ODI4&closeto=-135.91399182799995,63.656185331000046&radius=150&lookat=-135.91399182799995,63.656185331000046
        //TODO: We can use view moveTo here instead of doing it ourself
        http
          .get(this.mapillaryImgAPI, {
            params: {
              client_id: this.cliendId,
              closeto: [coordinates[0], coordinates[1]].toString(),
              organization_keys: this.organizationKey,
              radius: 150
            }
          })
          .then(response => {
            if (response.data) {
              const features = geojsonToFeature(response.data, {
                featureProjection: "EPSG:3857",
                dataProjection: "EPSG:4326"
              });
              const closestFeature = findClosestFeatureToCoordinate(
                features,
                fromLonLat(coordinates)
              );
              // Closest feature is the first element in the response array.
              // const closestFeature =   features[0];
              if (closestFeature) {
                const key = closestFeature.get("key");
                if (key) {
                  this.mapillary.moveToKey(key).then(
                    node => {
                      // Calculate Bearing between photo center and the marker
                      const start = toLonLat(
                        closestFeature.getGeometry().getCoordinates()
                      );
                      const end = coordinates;
                      const bearing = calculateBearing(
                        start[1],
                        start[0],
                        end[1],
                        end[0]
                      );

                      // Rotate according to the bearing
                      this.setBearing(node, bearing);
                      mapillaryStyleDefs.activeSequence = node.sequenceKey;
                      //Update layer
                      this.baseOverlayerLayer.changed();
                    },
                    error => {
                      console.error(error);
                    }
                  );
                }
              }
            }
          })
          .catch(error => {
            console.log(error);
          });
      }
    },

    /**
     * Remove or add markers based on layer state.
     * TODO: We can also consider doing this for individual layers.
     * rather then calling RemoveAll.
     */

    layerItemSelected(layer) {
      if (layer.getVisible() === true) {
        //2- Add markers
        this.createMarkers(layer.getSource().getFeatures());
      } else {
        this.markerComponent.removeAll();
      }
    },

    /**
     * Resize method
     */
    resize() {
      this.mapillary.resize();
    },

    /** BEARING  */
    setBearing(node, bearing) {
      if (!node.fullPano) {
        // We are only interested in setting the bearing for full 360 panoramas.
        return;
      }
      var desiredBearing = bearing; // Your desired bearing.
      var nodeBearing = node.ca; // Computed node compass angle (equivalent
      // to bearing) is used by mjs when placing
      // the node in 3D space.

      var basicX = this.bearingToBasic(desiredBearing, nodeBearing);
      var basicY = 0.5; // Vertical center

      var center = [basicX, basicY];

      this.mapillary.setCenter(center);
    },
    /**
     * Convert a desired bearing to a basic X image coordinate for
     * a specific node bearing.
     *
     * Works only for a full 360 panorama.
     */
    bearingToBasic(desiredBearing, nodeBearing) {
      // 1. Take difference of desired bearing and node bearing in degrees.
      // 2. Scale to basic coordinates.
      // 3. Add 0.5 because node bearing corresponds to the center
      //    of the image. See
      //    https://mapillary.github.io/mapillary-js/classes/viewer.html
      //    for explanation of the basic coordinate system of an image.
      var basic = (desiredBearing - nodeBearing) / 360 + 0.5;

      // Wrap to a valid basic coordinate (on the [0, 1] interval).
      // Needed when difference between desired bearing and node
      // bearing is more than 180 degrees.
      return this.wrap(basic, 0, 1);
    },
    /**
     * Wrap a value on the interval [min, max].
     */
    wrap(value, min, max) {
      var interval = max - min;

      while (value > max || value < min) {
        if (value > max) {
          value = value - interval;
        } else if (value < min) {
          value = value + interval;
        }
      }

      return value;
    }
  },

  destroyed() {
    unByKey(this.mapClickListenerKey);
    unByKey(this.mapHoverListenerKey);
    this.map.removeLayer(this.movePointLayer);
    this.map.removeLayer(this.baseOverlayerLayer);
    this.map.removeLayer(this.hoverHighlightLayer);
    this.map.removeOverlay(this.imagePreviewOverlay);
    window.removeEventListener("resize", this.resize());
    this.mapillary.off(Viewer.nodechanged, this.mapillaryChanged);
    this.mapillary.off(Viewer.changed, this.mapillaryMoveend);
    this.mapillary.getComponent("sequence").stop();
    EventBus.$off("featureClicked", this.goToOlFeature);
    EventBus.$off("layerItemSelected", this.layerItemSelected);
    this.mapillary.getComponent("marker").removeAll();
  }
};
</script>
<style lang="css" scoped></style>
